<style lang=less>
  @import '../../../assets/index.less';
  @import './bindTel.less';
</style>
<template>
  <div class="base-info bindTel">
    <!-- 绑定手机号 -->
    <p class="title">
      <span></span>{{$t('center.bindTel[0]')}}
    </p>
    <el-form  class="bindTelForm" :model="bindTel">
      <el-form-item :label="$t('center.bindTel[1]')">
        <el-input v-model="bindTel.tel" :placeholder="$t('center.bindTel[2]')"></el-input>
      </el-form-item>
      <div style="overflow: hidden;">
        <el-col :span="18">
        <el-form-item :label="$t('center.bindTel[3]')">
          <el-input v-model="bindTel.code" :placeholder="$t('center.bindTel[4]')"></el-input>
        </el-form-item>
        </el-col>
        <el-col :span="6" class="codeBtn">
          <el-button type="primary" style="width: 112px;" :loading="getCodeLoading" :disabled="!ableGetCode" @click="getCodeRequest">{{getCodeText}}</el-button>
        </el-col>
      </div>
      <el-form-item :label="$t('center.bindTel[5]')">
        <el-input v-model="bindTel.moneyPwd" show-password :placeholder="$t('center.bindTel[6]')"></el-input>
      </el-form-item>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.bindTel[7]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {sendMsg} from '@/api/public.js'
import {bindMobile} from "@/api/user.js"
export default {
  data () {
    return {
      submitLoading:false,
      bindTel:{//表单中数据
        tel:'',
        code:'',
        moneyPwd:''
      },
      countDownNumber:60,//倒计时秒数
      countDownTimer:null,//定时器
      getCodeText: this.$t('center.bindTel[8]'),//验证码处显示的内容
      ableGetCode:true,//验证码按钮能否点击
      getCodeLoading:false,
      sessionData:{},
    }
  },
  created () {
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
  },
  watch: {
  },
  methods: {
    submit(){//提交

      if(!this.$public.checkMobile(this.bindTel.tel)){
        this.$public.msg(this.$t('center.bindTel[9]'),'warning',this)
        return
      }

      if(!this.$public.code(this.bindTel.code)){
        this.$public.msg(this.$t('center.bindTel[10]'),'warning',this)
        return
      }

      if(!this.$public.pwd(this.bindTel.moneyPwd)){
        this.$public.msg(this.$t('center.bindTel[11]'),'warning',this)
        return
      }
      this.$public.googleVerify().then((r)=> {
        if(r){//验证通过
            this.submitRequest()
        } else{
            this.$public.msg(this.$t('center.bindTel[12]'),'error',this)
        }
      }).catch((r)=>{
        console.log('取消')
      })
      
    },
    submitRequest(){
      this.submitLoading = true
      let _data = {
        account: JSON.parse(sessionStorage.getItem('account')),
        tpwd: this.$public.Cryptographiccapital(this.bindTel.moneyPwd),
        code: this.bindTel.code,
        mcode:this.sessionData.mcode,
        mobile: this.bindTel.tel
      }
      bindMobile(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.bindTel[13]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.bindTel.moneyPwd = ''
          this.bindTel.code = ''
        }
      })
    },
    countDown(){//验证码倒计时
      this.ableGetCode = false
      let countDownCopy = this.countDownNumber
      this.countDownTimer = setInterval(()=>{
        if(this.countDownTimer && countDownCopy<=0){
          clearInterval(this.countDownTimer)
          this.getCodeText = this.$t('center.bindTel[14]')
          this.ableGetCode = true
          return
        }
        this.getCodeText = countDownCopy
        countDownCopy--;
      },1000)
    },
    getCodeRequest(){//发送获取验证码请求接口
      if(!this.$public.checkMobile(this.bindTel.tel)){
        this.$public.msg(this.$t('center.bindTel[9]'),'warning',this)
        return
      }
      this.getCodeLoading = true
      this.ableGetCode = false
      let _data = {
        mobile: this.bindTel.tel,
        mcode: this.sessionData.mcode,
        type:3
      }
      sendMsg(_data).then((res)=>{
        this.getCodeLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.bindTel[15]'),'success',this)
          this.countDown()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.ableGetCode = true
        }
      },
      ()=>{
        this.$public.msg(this.$t('center.bindTel[16]'),'error',this)
        this.getCodeLoading = false
        this.ableGetCode = true
      })
    }
  }
}
</script>



